/* src/components/Status/StatusList.css */

/* 전체 컨테이너 */
.status-list-container {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

/* 카드 목록 (flex 컨테이너) */
.status-list {
  display: flex;
  flex-direction: column;
  padding: 0;
  gap: 0;
}

/* 헤더와 카드에 적용될 공통 그리드 레이아웃 */
.status-list-header,
.status-card {
  display: grid;
  /* 장비정보 (220px), 데이터그룹(1fr), 작업지시(120px)로 컬럼 분할 */
  grid-template-columns: 220px 1fr 120px;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  text-align: center;
  border-bottom: 1px solid #ddd;
}

/* 헤더에만 적용될 스타일 */
.status-list-header {
  font-weight: bold;
  background-color: #f9fafb;
  border-bottom-width: 2px;
  color: #6B7280;
}

/* 헤더와 카드의 데이터 그룹(6개 컬럼)에 적용될 공통 레이아웃 */
.header-item-group,
.card-content-group {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 1rem;
  align-items: center;
}

.status-card {
  background-color: #fff;
}

/* 마지막 카드의 하단 테두리 제거 */
.status-list .status-card:last-child {
  border-bottom: none;
}