/* src/components/Sidebar.css */
.sidebar {
  width: 250px;
  background-color: #2E2E2E;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  height: 100vh; /* 화면 전체 높이 */
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar-logo {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
  color: #FFFFFF;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
}

.nav-link {
  color: #E0E0E0; /* 밝은 회색 */
  text-decoration: none;
  padding: 1rem 1.5rem;
  margin-bottom: 0.5rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: background-color 0.3s, color 0.3s;
}

.nav-link:hover {
  background-color: #3a3a3a;
  color: #FFFFFF;
}

.nav-link.active {
  background-color: #00BFFF; /* DeepSkyBlue */
  color: #FFFFFF;
  font-weight: bold;
}
