/* src/components/Status/StatusCard.css */
.card-machine-info {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 0.25rem;
  text-align: left;
}

.machine-name {
  margin: 0;
  font-size: 1.1rem;
  font-weight: bold;
  white-space: norawp;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 상태 태그(가동중, 오류 등) 스타일 */
.status-tag {
  flex-shrink: 0;
  display: inline-block;
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: bold;
  color: white;
  min-width: 60px;
  text-align: center;
}

.status-card-running { background-color: #28a745; }
.status-idle { background-color: #6c757d; }
.status-error { background-color: #dc3545; }
.status-disconnected { background-color: #ffc107; color: #333; }

/* 데이터 값 스타일 */
.card-content-group span {
  font-size: 0.9rem;
  color: #333;
  white-space: nowrap; /* 내용이 길어도 한 줄로 표시 */
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 작업 지시 버튼 스타일 */
.card-action button {
  padding: 8px 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  background-color: #fff;
  transition: background-color 0.2s;
}

.card-action button:hover {
  background-color: #f0f0f0;
}